<template>
  <section class="about section" id="about">
    <h2 class="section__title">About Me</h2>
    <span class="section__subtitle">My introduction</span>

    <div class="about__container container grid">
      <!-- <img src="assets/img/about.jpg" alt="" class="about__img"> -->
      <div class="about__data">
          <p class="about__description">{{ description }}</p>
          <div class="about__info">
            <div v-for="infoItem in info" :key="infoItem.id">
                <span class="about__info-title">{{ formatNumber(infoItem.count) }}</span>
                <span class="about__info-name">{{ getFirst(infoItem.name) }} <br> {{ getSecond(infoItem.name) }}</span>
            </div>
          </div>
          <div class="about__buttons">
              <a download="" href="../../assets/pdf/yichwen-cv.pdf" class="button button--flex">
                  Download CV<i class="uil uil-download-alt"></i>
              </a>
          </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data: function() {
    return {
      description: 'Software developer, with extensive knowledge and years of experience, working in ' + 
              'web technologies and software design, delivering quality work. Interested in new ' + 
              'software technologies, frameworks, and innovation ideas.',
      info: [
        { id: 0, name: 'Years experience', count: 10 },
        { id: 1, name: 'Completed projects', count: 10 },
        { id: 2, name: 'Companies worked', count: 3 }
      ]
    }
  },
  methods: {
    formatNumber(count) {
      return ('' + count).padStart(2, '0') + '+'
    },
    getFirst(str) {
      return str.split(' ')[0]
    },
    getSecond(str) {
      return str.split(' ')[1]
    }
  }
}
</script>

<style scoped>

.about__img {
    width: 200px;
    border-radius: .5rem;
    justify-self: center;
    align-self: center;
}

.about__description {
    text-align: center;
    margin-bottom: var(--mb-2-5);
}

.about__info {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: var(--mb-2-5);
}

.about__info-title {
    font-size: var(--h2-font-size);
    font-weight: var(--font-semi-bold);
    color: var(--title-color)
}

.about__info-name {
    font-size: var(--smaller-font-size);
}

.about__info-title,
.about__info-name {
    display: block;
    text-align: center;
}

.about__buttons {
    display: flex;
    justify-content: center;
    align-items: center;
}

</style>
