<template>
  <header class="header" id="header">
    <nav class="nav container">
      <a href="#" class="nav__logo">yichwen</a>
      <div :class="[{ 'show-menu': showMenu }, 'nav__menu']" id="nav-menu">
          <ul class="nav__list grid">
            <li class="nav__item" v-for="navItem in navItems" :key="navItem.key">
              <a :href="navItem.link" class="nav__link active-link" @click="closeMenu()">
                  <i :class="navItem.icon" class="nav__icon"></i> {{ navItem.name }}
              </a>
            </li>
          </ul>
          <i class="uil uil-times nav__close" id="nav-close" @click="closeMenu()"></i>
      </div>
      <div class="nav__btns">
          <!-- Theme change button -->
          <i class="change-theme" :class="currentIcon" id="theme-button" @click="changeTheme()"></i>
          <div class="nav__toggle" id="nav-toggle" @click="openMenu()">
              <i class="uil uil-apps"></i>
          </div>
      </div>
    </nav>
</header>
</template>

<script>
export default {
  name: 'Header',
  data: function() {
    return {
      navItems: [
        { id: 0, name: 'Home', link: '#home', icon: 'uil uil-estate' },
        { id: 1, name: 'About', link: '#about', icon: 'uil uil-user'},
        { id: 2, name: 'Skills', link: '#skills', icon: 'uil uil-file-alt' },
        // { id: 3, name: 'Services', link: '#services', icon: 'uil uil-file-alt' },
        // { id: 4, name: 'Portfolio', link: '#portfolio', icon: 'uil uil-scenery' },
        { id: 5, name: 'Contact', link: '#contact', icon: 'uil uil-message' }
      ],
      showMenu: false,
      darkTheme: 'dark-theme',
      selectedTheme: '',
      // dark theme show sun
      darkThemeIcon: 'uil uil-sun',
      // light theme show moon
      lightThemeIcon: 'uil uil-moon'
    }
  },
  mounted() {
    // theme is either 'dark' / 'light'
    const theme = localStorage.getItem('selected-theme')
    // theme exist, use theme, else use light as default theme
    this.selectedTheme = theme ? theme :  'light'
    this.changeBodyTheme()
  },
  methods: {
    openMenu() {
      this.showMenu = true
    },
    closeMenu() {
      this.showMenu = false
    },
    changeTheme() {
      this.selectedTheme = this.isDarkTheme() ? 'light': 'dark'
      this.changeBodyTheme()
    },
    isDarkTheme() {
      return this.selectedTheme && this.selectedTheme === 'dark'
    },
    changeBodyTheme() {
      document.body.classList[this.isDarkTheme() ? 'add' : 'remove'](this.darkTheme)
    }
  },
  computed: {
    currentIcon() {
       return this.isDarkTheme() ? this.darkThemeIcon : this.lightThemeIcon
    }
  }
}
</script>

<style scoped>
/*==================== NAV ====================*/

/* make the header at the bottom left position in small screen */
.header {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}

.nav {
  max-width: 968px;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo,
.nav__toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__logo:hover {
    color: var(--first-color);
}

.nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
}

.nav__toggle:hover {
    color: var(--first-color)
}

.nav__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__link:hover {
    color: var(--first-color);
}

.nav__icon {
    font-size: 1.2rem;
}

.nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--first-color);
}

.nav__close:hover {
    color: var(--first-color-alt);
}

/* show menu */
.show-menu {
    bottom: 0 !important;
}

/*========== Button Dark/Light ==========*/
.nav__btns {
    display: flex;
    align-items: center;
}

.change-theme {
    font-size: 1.25rem;
    color: var(--title-color);
    margin-right: var(--mb-1);
    cursor: pointer;
}

.change-theme:hover {
    color: var(--first-color)
}

@media screen and (max-width: 767px) {
  .nav__menu {
      position: fixed;
      bottom: -100%;
      left: 0;
      width: 100%;
      background-color: var(--body-color);
      padding: 2rem 1.5rem 4rem;
      box-shadow: 0 -1px 4px rgba(0, 0, 0, .15);
      border-radius: 1.5rem 1.5rem 0 0;
      transition: .3s;
  }
}

@media screen and (min-width: 768px) {

  .header {
    top: 0;
    bottom: initial;
  }

  .nav {
    height: calc(var(--header-height) + 1.5rem);
    column-gap: 1rem;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: none;
  }

  .nav__list {
    display: flex;
    column-gap: 2rem;
  }

  .nav__menu {
    margin-left: auto;
  }

}

</style>
